.good-wrap{
  width:46%; display:block;padding:.1rem .1rem 0;
  >div{
    border:.01rem solid #000;padding:.1rem .1rem 0;font:bold .16rem/1.5 "";color:#f0ad4e;
    img{width:100%;height:2rem;}
    .price-sail{
      display:flex;
      .price{margin-right:.5rem;}
    }
    .title{width:80%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;}
  }
}