.input-wrap{
  width:50%;margin:.1rem auto;display:flex;height:.3rem;font:.15rem/2 "";
  span{display:inline-block;text-align-last:justify;width:.5rem;margin-right:.2rem;}
  input{flex:1;border:.01rem solid #fc8c84;outline:0;background:#f3f3f3;text-indent:1em;}
  input:focus{border:.02rem solid #f0ad4e;}
}