.box-wrap{
  height:.5rem;
  .header-wrap{
    position:fixed;left:0;right:0;top:0;z-index:999;font:bold .16rem/.5rem "";background:#5e81ac;
    ul{
      display:flex;justify-content:space-around;
      a{
        display:block;color:#fff;
        &.active{color:#0000ff;border-bottom:.05rem solid #0000ff;}
      }
    }
  }
}