.detail-wrap{
  .detail{
    .img-wrap{
      position:relative;border:.02rem solid #2b81af;;
      img{width:100%;}
    }
    .info-wrap{
      background:#F0F0F0;padding:.1rem .2rem;color:#f00;font-weight:bold;
      b{color:#000;}
      .title{width:80%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;}
      .price,.sail{line-height:2;}
      .amount-wrap{
        line-height:.4rem;
        input{
          background:#f0ad4e;height:.2rem;
          &.increment{margin-right:.1rem;}
          &.amount{width:1rem;text-align:center;margin-right:.1rem;box-sizing:border-box;}
          &.reduce{}
        }
      }
      .addToCart{
        width:50%;height:.4rem;background:#ffff4f;border:0;outline:none;font:bold .2rem/2 "";
      }
    }
  }
}