.reg-wrap{
  border:.01rem solid #cacaca;padding:0.1rem 0;
  .img-wrap{text-align:center;}
  &>form{
    margin-top:.2rem;
    .username-tips,.pwd-tips{display:block;width:50%;margin:0 auto;height:.26rem;font:.13rem/2 "";color:#f00;}
    .reg-btn{display:block;width:50%;margin:0 auto;height:.3rem;font:bold .15rem/2 "";
      background:#fc8c84;border:0;outline:none;text-align:center;}
    .other-action{
      width:50%;display:flex;justify-content:flex-end;margin:0 auto;font:.13rem/2 "";color:#0000fe;
    }

  }
}
