.user-wrap{
  .user__info-wrap{
    background:#fc8c84;padding:.1rem 0;font:.14rem/1.5 "";
    .layout{
      display:flex;justify-content:space-between;align-items:center;
      .headPortrait{
        text-align:center;
        img{display:block;width:100%;height:100%;margin:0 auto;}
        #upload-headPortrait{width:.7rem;margin:.05rem auto;}
      }
      .user-info{
        text-align:center;
        .nickName{font-size:.22rem;font-weight:bold;color:#5e81ac;}
      }
      .other-info{
        color:#b5d592;font-weight:bold;
        .tips{display:inline-block;width:.7rem;text-align-last:justify;color:#000;}
      }
    }
  }
  .user__main-wrap{
    padding:.2rem 0;
    .order-wrap{
      border:.01rem solid #B9C1C6;font:.16rem/2 "";padding:.1rem;
      .order-title{
        display:flex;justify-content:space-between;border-bottom:.02rem solid #f0ad4e;color:#ffc520;
      }
      .order-cont{
        display:flex;justify-content:space-between;color:#5E740B;text-align:center;
        em{display:block;width:.8rem;height:.8rem;margin:0 auto;background:url("../../assets/imgs/user_icons.png") no-repeat;}
      }
    }
    .commonFunctionsWrap{
      border:.01rem solid #B9C1C6;font:.16rem/2 "";padding:.1rem;
      .functions-title{
        display:flex;justify-content:space-between;border-bottom:.02rem solid #f0ad4e;color:#ffc520;
      }
      .functions-cont{
        display:flex;justify-content:space-between;color:#5E740B;text-align:center;flex-wrap:wrap;
        li{
          width:25%;
          em{display:block;width:.8rem;height:.8rem;margin:0 auto;background:url("../../assets/imgs/user_icons.png") no-repeat;}
        }
      }
    }
  }
}